import '../styles.scss'
import { installManifest, plistMap } from './constants'

export const downloadApp = document.querySelector('#download')
export const header = document.querySelector('.header')
export const title = document.querySelector('.header__title')
export const logo = document.querySelector('.header__logo')
export const subTitle = document.querySelector('.header__subtitle')
export const firstCard = document.querySelector('.card__first')
export const cardField = document.querySelector('.card-field')

window.addEventListener('scroll', () => {
  if ((window.scrollY >= 8) & (window.scrollY < 1000)) {
    if (header.classList.contains('header--full'))
      header.classList.remove('header--full')
    if (header.classList.contains('header--short'))
      header.classList.remove('header--short')
    if (!firstCard.classList.contains('card__first--short'))
      firstCard.classList.add('card__first--short')
    if (cardField.classList.contains('card-field--full'))
      cardField.classList.remove('card-field--full')
  } else if (window.scrollY >= 1000) {
    if (header.classList.contains('header--full'))
      header.classList.remove('header--full')
    if (!header.classList.contains('header--short'))
      header.classList.add('header--short')
    if (logo.classList.contains('header__logo--visible'))
      logo.classList.remove('header__logo--visible')
    if (title.classList.contains('header__title--full'))
      title.classList.remove('header__title--full')
    if (subTitle.classList.contains('header__subtitle--full'))
      subTitle.classList.remove('header__subtitle--full')
  } else if (window.scrollY < 8) {
    if (!header.classList.contains('header--full'))
      header.classList.add('header--full')
    if (logo.classList.contains('header__logo--hidden'))
      logo.classList.remove('header__logo--hidden')
    if (!logo.classList.contains('header__logo--visible'))
      logo.classList.add('header__logo--visible')
    if (!title.classList.contains('header__title--full'))
      title.classList.add('header__title--full')
    if (!subTitle.classList.contains('header__subtitle--full'))
      subTitle.classList.add('header__subtitle--full')
    if (firstCard.classList.contains('card__first--short'))
      firstCard.classList.remove('card__first--short')

    if (!cardField.classList.contains('card-field--full'))
      cardField.classList.add('card-field--full')
  }
})

const plist =
  plistMap[new URLSearchParams(window.location.search).get('plistId')]

const startInstall = () => {
  if (!navigator.userAgent.includes('Safari')) {
    alert('Устройство может не поддерживаться')
  }
}
if (plist) {
  subTitle.innerHTML = '«РСХБ ДБО ФЛ Учёт надоя» на iphone'
  downloadApp.href = installManifest + encodeURIComponent(plist)
} else {
  subTitle.innerHTML = 'Ссылка на «РСХБ ДБО ФЛ Учёт надоя» не найдена'
  downloadApp.classList.add('card__href--disabled')
}

downloadApp.addEventListener('click', startInstall)
window.addEventListener('load', () => downloadApp.click())
